<template>
  <van-list
    class="shopcart"
    v-model="loadOptions.isLoading"
    :finished="loadOptions.isFinished"
    :finished-text="loadOptions.finishedText"
    @load="loadData"
    :offset="50"
  >
    <!--  -->
    <div v-if="shopcartData.length == 0">
      <van-empty :image="require('../../assets/images/shopcar.png')" description="" />
      <div class="recommend">
        <!--  -->
        <div class="likes">
          <img src="https://img.alicdn.com/tfs/TB1V2eQrKSSBuNjy0FlXXbBpVXa-966-114.png" alt />
        </div>
        <!--  -->
        <div class="content">
          <div
            class="fl content-item"
            @click="goDetail(item.pid)"
            :key="index"
            v-for="(item, index) in hotGoodsData"
          >
            <img class="auto-img" v-lazy="item.smallImg" alt />
            <div class="pro-name one-text">{{item.name}}</div>
            <div class="pro-enname one-text">{{item.enname}}</div>
            <div class="pro-price">{{item.price}}</div>
          </div>
        </div>
      </div>
    </div>
    <div v-else>
      <van-nav-bar @click-right="manage" :right-text="isManage ? '' : ''" />

      <!--  -->
      <div class="list">
        <van-swipe-cell class="list-items" v-for="(item, index) in shopcartData" :key="index">
          <div class="list-item clearfix">
            <!--  -->
            <div class="fl checkbox-item">
              <van-checkbox
                checked-color="orange"
                v-model="item.isCheck"
                @change="simpleSelect"
                shape="round"
              />
            </div>
            <!--  -->
            <div class="fl product-info clearfix">
              <div class="product-img fl">
                <img class="auto-img" :src="item.small_img" alt />
              </div>
              <div class="fl product-content">
                <div class="product-text">
                  <div class="clearfix product-name">
                    <span class="product-title fl">{{item.name}}</span>
                    <span class="fl product-rule">{{item.rule}}</span>
                  </div>
                  <div class="product-enname">{{item.enname}}</div>
                </div>

                <div class="product-price">
                  <div class="price fl">{{item.price}}</div>
                  <div class="fr product-count">
                    <van-stepper
                      v-model="item.count"
                      :integer="true"
                      theme="round"
                      button-size="22"
                      disable-input
                      @change="modifyCount(item)"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <template #right>
            <van-button square type="danger" text="" @click="removeShopcart([item.sid], index)" />
          </template>
        </van-swipe-cell>
      </div>

      <!--  -->
      <div v-if="isManage">
        <van-submit-bar
          class="submit-bar"
          :price="total"
          :disabled="isNotHasCheck"
          button-text=""
          button-type="info"
          @submit="sumbitOrder"
        >
          <van-checkbox checked-color="orange" v-model="allCheck" @click="allSelect"></van-checkbox>
        </van-submit-bar>
      </div>
      <div v-else>
        <van-submit-bar
          class="submit-bar submit-bar-delete"
          :disabled="isNotHasCheck"
          button-text=""
          button-type="info"
          @submit="removeMoreShopcart"
        >
          <van-checkbox checked-color="orange" v-model="allCheck" @click="allSelect"></van-checkbox>
        </van-submit-bar>
      </div>
    </div>
  </van-list>
</template>

<script>
import "../../assets/css/shopcart.less";

export default {
  data() {
    return {
      isManage: true,
      allCheck: false,
      isHas: false,

      isNotHasCheck: true,

      shopcartData: [],

      allShopcartData: [],

      hotGoodsData: [],

      loadOptions: {
        //load
        isLoading: true,

        //
        isFinished: false,

        finishedText: ""
      },

      // 8
      loadCount: 8,

      //
      total: 0
    };
  },

  created() {
    //
    this.getShopcartData();

    this.getHotGoods();
  },

  methods: {
    //
    getShopcartData() {
      //token
      let tokenString = localStorage.getItem("__Tk");

      //
      this.$toast.loading({
        message: "...",
        forbidClick: true,

        //0
        duration: 0
      });

      this.axios({
        method: "GET",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString
        }
      })
        .then(result => {
          this.$toast.clear();

          if (result.data.code == 5000) {
            result.data.result.forEach(v => {
              v.isCheck = false;
            });

            this.allShopcartData = result.data.result;

            this.shopcartData = this.allShopcartData.splice(0, this.loadCount);

            
            // 

            //
            if (this.allShopcartData.length > 0) {
              this.loadOptions.isLoading = false;
            } else {
              this.loadOptions.isLoading = true;
              this.loadOptions.isFinished = true;
            }

            if (this.shopcartData.length == 0) {
              this.loadOptions.finishedText = "";
            }
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //
    loadData() {
      
      setTimeout(() => {
        this.shopcartData.push(
          ...this.allShopcartData.splice(0, this.loadCount)
        );
        if (this.allShopcartData.length == 0) {
          //
          this.loadOptions.isLoading = true;
          this.loadOptions.isFinished = true;
        }
      }, 1000);
    },

    //
    allSelect() {
      

      this.allShopcartData.forEach(v => {
        v.isCheck = this.allCheck;
      });

      this.shopcartData.forEach(v => {
        v.isCheck = this.allCheck;
      });

      this.isNotHasCheck = !this.allCheck;

      this.sum();
    },

    //
    simpleSelect() {
      this.sum();

      this.isNotHasCheck = true;

      for (let i = 0; i < this.shopcartData.length; i++) {
        if (this.shopcartData[i].isCheck) {
          this.isNotHasCheck = false;
          break;
        }
      }

      for (let i = 0; i < this.shopcartData.length; i++) {
        if (!this.shopcartData[i].isCheck) {
          this.allCheck = false;
          return;
        }
      }

      this.allCheck = true;
    },

    // 
    manage() {
      this.isManage = !this.isManage;
    },

    //
    modifyCount(item) {
      

      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/modifyShopcartCount",
        data: {
          appkey: this.appkey,
          tokenString,
          sid: item.sid,
          count: item.count
        }
      })
        .then(result => {
          this.$toast.clear();
          
          if (result.data.code == 6000) {
            this.sum();
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //
    removeShopcart(sids, index) {
      //sid
      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/removeShopcart",
        data: {
          appkey: this.appkey,
          tokenString,

          //sid
          sids: JSON.stringify(sids)
        }
      })
        .then(result => {
          this.$toast.clear();

          if (result.data.code == 7000) {
            this.shopcartData.splice(index, 1);
            if (this.shopcartData.length == 0) {
              this.loadOptions.finishedText = "";
            }
            this.sum();
            this.$toast.success("");
          } else {
            this.$toast.success("");
          }
          
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    // 
    removeMoreShopcart() {
      //sid
      let sids = [];
      this.shopcartData.forEach(v => {
        if (v.isCheck) {
          sids.push(v.sid);
        }
      });

      

      let tokenString = localStorage.getItem("__Tk");

      this.$toast.loading({
        message: "...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "POST",
        url: "/removeShopcart",
        data: {
          appkey: this.appkey,
          tokenString,

          //sid
          sids: JSON.stringify(sids)
        }
      })
        .then(result => {
          this.$toast.clear();

          if (result.data.code == 7000) {
            //
            for (let i = this.shopcartData.length - 1; i >= 0; i--) {
              if (this.shopcartData[i].isCheck) {
                this.shopcartData.splice(i, 1);
              }
            }

            this.sum();

            if (this.shopcartData.length == 0) {
              this.allCheck = false;
              this.isManage = true;
              this.loadOptions.finishedText = "";
            }

            this.$toast.success("");
          } else {
            this.$toast.success("");
          }
          
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },

    //
    sum() {
      this.total = 0;
      this.shopcartData.forEach(v => {
        if (v.isCheck) {
          this.total += v.count * v.price * 100;
        }
      });
    },
    // 
    getHotGoods() {
      //  axios
      this.axios({
        method: "GET",
        url: "/typeProducts",
        params: {
          appkey: this.appkey,
          key: "isHot",
          value: 1
        }
      })
        .then(res => {
          this.$toast.clear();
          

          if (res.data.code == 500) {
            this.hotGoodsData = res.data.result;
            
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
    },
    //
    sumbitOrder() {
      //sid
      let sids = [];

      this.shopcartData.forEach(v => {
        if (v.isCheck) {
          sids.push(v.sid);
        }
      });

      
      this.$router.push({
        name: "Pay",
        query: {
          sids: sids.join("-")
        }
      });
    },

    goDetail(pid) {
      //pid: id
      this.$router.push({ name: "Detail", query: { pid } });
    }
  }
};
</script>
